
let addElement = () =>{
      
    let form = document.querySelector('.form__items');
    let inputs = form.getElementsByTagName('input');
    let textArea = form.getElementsByTagName('textarea');
    let inputClear = (items,area)=>{// input  textarea
      for(let i=0;i<items.length; i++ ){
          items[i].value ='';
       }
       area[0].value='';
    }
    let inputCheck = (items, area)=>{//    inputs
      for(let i=0;i<items.length; i++ ){
           if ((items[i].value == '') ||(area[0].value =='')) {
             
              return false;
              break;
          }
          else if((items[i].value != '') && (area[0].value !='')){
           
             return true;
              break;
          }
      }

    }

  let  myPromise = (url)=>{//   
             
                 fetch(url,{
                  method:'post'
                })
                    .then(function(response){
                      return response.json() // json   
                    })
                    .then(function(data){//       
                            console.log(data);
                          
                    })
      
    }


// ----------------------------------
    inputClear(inputs,textArea);//  
      
                            
    
    form.addEventListener('submit', formSend);

       async function formSend(event){// ,   promise
            event.preventDefault();
            if(inputCheck(inputs,textArea)){//  
               let formData = new FormData(form);
               inputClear(inputs,textArea);//     
               let response = await fetch('../../php/addData.php',{// response()     
                                        method:'POST',
                                        body: formData
                                        });
                                         
              if (response.ok && response.status == 200) { //    
              //   
               //myPromise('../../php/getdata.php');//  
              
              }                         
            }
           
          
        }
  
  

}
module.exports = addElement;

